.settings-panel {
    background-color: #f8f9fa;
    padding: 20px;
    border-radius: 12px;
    margin-bottom: 24px;
    animation: slideDown 0.3s ease-out;
  }
  
  .settings-row {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin: 10px 0;
  }
  
  .time-select {
    padding: 8px 16px;
    border: 2px solid #ddd;
    border-radius: 8px;
    margin-left: 12px;
    font-size: 16px;
    transition: border-color 0.2s;
  }
  
  .time-select:focus {
    outline: none;
    border-color: #3498db;
  }
  
  .party-mode-button {
    padding: 8px 16px;
    border: 2px solid #3498db;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .party-mode-button:hover {
    background-color: #3498db1a;
  }
  
  .party-mode-button.active {
    background-color: #3498db;
    color: white;
  }
  
  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }